
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import { getStore, setStore } from '../../config/mUtils'
import { getFootprintList, delFoot} from '../../service/getData.js'
import { formalname } from '../../config/env.js'

export default {
components: {
XHeader,
XButton,
Checker,
CheckerItem
},
mounted() {
this.$vux.loading.show({
text: '加载中...'
})

getFootprintList(formalname===1?1037:getStore('userId'), 1).then(resolved => {
console.log(resolved.data)
this.list = resolved.data;

})
this.$vux.loading.hide()
},
methods: {
openDetail(index) {
console.log(index)
this.$router.push({
name: 'Commodity',
query: {
id: index
}
});
},
showMenus(index) {
console.log('更多')
this.isedit=!this.isedit
},
del() {


delFoot(formalname===1?1037:getStore('userId'), this.ids).then(resolved => {
this.ids.split('#').forEach((ele)=>{
this.list.forEach((element,index)=>{
if(element.id==ele){
this.list.splice(index,1)
this.$nextTick(()=>{
this.$vux.toast.show({
text:'删除成功'
})
})
}
})
})
})

},

onItemClick (value) {
if(value==='all'){
if(this.ischeck){
this.ischeck=false
this.checkbox = []
this.allVal = 0
}else{
this.checkbox = []
this.ischeck=true;
this.list.forEach(function(element) {
this.checkbox.push(element.id)
this.ids += element.id+'#'
}, this);
}
}
},
},
watch:{
checkbox(){
let idNum = ''
this.checkbox.forEach((ele)=>{
idNum += ele+'#'
return this.ids = idNum
})
}
},
data() {
return {
title: '我的足迹',
list: [],
ids:'',
isempty: false,
isedit: false,
ischeck: false,
checkbox: [],
checkboxAll:[],
}
}
}



